.menu-toggle {
  display: none;
}

.main-navigation {
  display: flex;
  margin-right: auto;
  margin-left: auto;
}
.main-navigation .children {
  opacity: 0;
  visibility: hidden;
  background: green;
  position: absolute;
  transition: opacity 0.2s 0.1s;
  color: white;
}
.main-navigation .children a {
  display: inline-block;
  padding: var(--spacer);
  color: white;
}
.main-navigation li:hover .children {
  visibility: visible;
  opacity: 1;
}
.main-navigation .nav-cta {
  margin-left: auto;
}

.menu ul,
.menu ul > li {
  margin: 0;
  position: relative;
  list-style-type: none;
}

.menu > ul {
  display: flex;
}
.menu > ul > li {
  padding: var(--spacer);
  text-decoration: underline;
  text-underline-offset: 0px;
  text-decoration-color: transparent;
  transition: all 0.2s;
}
.menu > ul > li:hover {
  text-decoration: underline;
  text-underline-offset: 8px;
  text-decoration-color: red;
}

.site-footer .current_page_item,
.main-navigation .current_page_ancestor {
  text-decoration: underline;
  text-underline-offset: 8px;
  text-decoration-color: orange;
}

.footer-menu > ul > li > a {
  opacity: 0.67;
}
.footer-menu .children a {
  opacity: 1;
}