.menu-toggle {
	display: none;
}

.main-navigation {
	display: flex;
	margin-right: auto;
	margin-left: auto;

	.children {
		opacity: 0;
		visibility: hidden;
		background: green;
		position: absolute;
		transition: opacity .2s .1s;
		color: white;

		a {
			display: inline-block;
			padding: var(--spacer);
			color: white;
		}
	}

	li:hover .children {
		visibility: visible;
		opacity: 1;
	}

	.nav-cta {
		margin-left: auto;
	}
}

.menu ul,
.menu ul > li {
	margin: 0;
	position: relative;
	list-style-type: none;
}

.menu > ul {
	display: flex;

	& > li {
		padding: var(--spacer);
		text-decoration: underline;
		text-underline-offset: 0px;
		text-decoration-color: transparent;
		transition: all .2s;

		&:hover {
			text-decoration: underline;
			text-underline-offset: 8px;
			text-decoration-color: red;
		}
	}
}

.site-footer .current_page_item,
.main-navigation .current_page_ancestor {
	text-decoration: underline;
	text-underline-offset: 8px;
	text-decoration-color: orange;
}

.footer-menu {
	& > ul > li > a {
		opacity: .67;
	}

	.children a {
		opacity: 1;
		// font-weight: 600;
	}
}